/* google font: Roboto */
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
	font-size: 62.5%;
}

body {
	font-family: "Roboto", sans-serif;
}

img {
	max-width: 100%;
}

.f-left {
	float: left;
}

.f-right {
	float: right;
}

.fix {
	overflow: hidden;
}

a,
.button,
button {
	cursor: pointer;
	display: inline-block;
	-webkit-transition: all 0.1s ease-out 0s;
	-moz-transition: all 0.1s ease-out 0s;
	-ms-transition: all 0.1s ease-out 0s;
	-o-transition: all 0.1s ease-out 0s;
	transition: all 0.1s ease-out 0s;
}

a:focus,
.button:focus {
	text-decoration: none;
	outline: none;
}

a:focus,
a:hover,
.portfolio-cat a:hover,
.footer -menu li a:hover {
	color: #2b96cc;
	text-decoration: none;
}

a {
	color: #1696e7;
	outline: medium none;
	text-decoration: none;
}

button:focus,
input:focus,
input:focus,
textarea,
textarea:focus {
	outline: 0;
}

.uppercase {
	text-transform: uppercase;
}

.capitalize {
	text-transform: capitalize;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: #313131;
	margin-top: 0;
	font-style: normal;
	font-weight: 400;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
	color: inherit;
}

h1 {
	font-size: 4rem;
	font-weight: 500;
}

h2 {
	font-size: 3.5rem;
}

h3 {
	font-size: 2.8rem;
}

h4 {
	font-size: 2.2rem;
}

h5 {
	font-size: 1.8rem;
}

h6 {
	font-size: 1.6rem;
}

ul {
	margin: 0;
	padding: 0;

	li {
		list-style: none;
	}
}

p {
	font-size: 1.4rem;
	font-weight: normal;
	line-height: 2.4rem;
	margin-bottom: 1.5rem;
}

hr {
	border-bottom: 1px solid #eceff8;
	border-top: 0 none;
	margin: 3rem 0;
	padding: 0;
}

label {
	cursor: pointer;
	font-size: 1.4rem;
	font-weight: 400;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

/* Firefox */
input[type=number] {
	-moz-appearance: textfield;
}

.br-theme-fontawesome-stars .br-widget a {
	font-family: "Font Awesome 5 Pro";
	font-weight: 900;
	font-size: 2rem;
	color: #929292;

	&.br-selected, &.br-active {
		color: #ff9933;
	}

	&::after {
		content: '\f005';
	}
}
